ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

input {
  border: none;
  outline: none;
  background-color: transparent;
}

textarea {
  border: none;
  outline: none;
  background-color: transparent;
}

html {
  font-size: 16px;
}

/* 媒体查询 */
@media (max-width: 9000px) {
  html {
    font-size: 50px;
  }
}
@media (max-width: 7000px) {
  html {
    font-size: 40px;
  }
}
@media (max-width: 4800px) {
  html {
    font-size: 30px;
  }
}
@media (max-width: 3600px) {
  html {
    font-size: 24px;
  }
}
@media (max-width: 2800px) {
  html {
    font-size: 18px;
  }
}

@media (max-width: 2100px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 1800px) {
  html {
    font-size: 13px;
  }
}

@media (max-width: 1400px) {
  html {
    font-size: 11px;
  }
}
@media (max-width: 1200px) {
  html {
    font-size: 8px;
  }
}
/* 大型设备（桌面，大于 900px） */
@media (max-width: 900px) {
  html {
    font-size: 12px;
  }
}

@media (max-width: 800px) {
  html {
    font-size: 9px;
  }
}

/* 中型设备（平板，600px 到 900px） */
@media (max-width: 700px) {
  html {
    font-size: 8px;
  }
} /* 小型设备（手机，小于 600px） */

@media (max-width: 600px) {
  html {
    font-size: 6px;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 4px;
  }
}

@media (max-width: 400px) {
  html {
    font-size: 3px;
  }
}

@media (max-width: 300px) {
  html {
    font-size: 2px;
  }
}
